body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
Theme Name: Yoga with Adriene
Theme URI: https://yogawithadriene.com/
Description: Custom theme forAAdriene Mishler.
Author: Authentik
Author URI: https://authentik.com/

Version: 1.1.7
Template: genesis

*/


/* HTML5 Reset
---------------------------------------------------------------------------- */

/* normalize.css v8.0.1 | MIT License | github.com/necolas/normalize.css
---------------------------------------------------------------------------- */
/* stylelint-disable */
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%
}

body {
  margin: 0
}

main {
  display: block
}

h1 {
  font-size: 2em;
  margin: .67em 0
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible
}

pre {
  font-family: monospace, monospace;
  font-size: 1em
}

a {
  background-color: transparent
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted
}

b,
strong {
  font-weight: bolder
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em
}

small {
  font-size: 80%
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline
}

sub {
  bottom: -.25em
}

sup {
  top: -.5em
}

img {
  border-style: none
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0
}

button,
input {
  overflow: visible
}

button,
select {
  text-transform: none
}

[type=button],
[type=reset],
[type=submit],
button {
  -webkit-appearance: button
}

[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner,
button::-moz-focus-inner {
  border-style: none;
  padding: 0
}

[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring,
button:-moz-focusring {
  outline: 1px dotted ButtonText
}

fieldset {
  padding: .35em .75em .625em
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal
}

progress {
  vertical-align: baseline
}

textarea {
  overflow: auto
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit
}

details {
  display: block
}

summary {
  display: list-item
}

[hidden],
template {
  display: none
}

/* stylelint-enable */

/* Box Sizing
--------------------------------------------- */

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

/* Float Clearing
--------------------------------------------- */

.author-box::before,
.clearfix::before,
.entry::before,
.entry-content::before,
.nav-primary::before,
.nav-secondary::before,
.pagination::before,
.site-container::before,
.site-footer::before,
.site-header::before,
.site-inner::before,
.widget::before,
.wrap::before {
  content: " ";
  display: table;
}

.author-box::after,
.clearfix::after,
.entry::after,
.entry-content::after,
.nav-primary::after,
.nav-secondary::after,
.pagination::after,
.site-container::after,
.site-footer::after,
.site-header::after,
.site-inner::after,
.widget::after,
.wrap::after {
  clear: both;
  content: " ";
  display: table;
}


/* Defaults
---------------------------------------------------------------------------- */

/* Typographical Elements
--------------------------------------------- */

html {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

body {
  background-color: #fff;
  color: #000;
  font-family: 'acumin-pro', sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.625;
  margin: 0;
  overflow-x: hidden;
}

a,
button,
input:focus,
input[type="button"],
input[type="reset"],
input[type="submit"],
textarea:focus,
.button,
.calendar,
.site-header {
  transition: all 0.2s ease-in-out;
}

a {
  color: #7692a8;
  text-decoration: underline;
}

a:focus,
a:hover {
  color: #000;
  text-decoration: none;
}

p {
  margin: 0 0 30px;
  padding: 0;
}

p.margin-ten {
  margin-bottom: 10px;
}

ol,
ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

hr {
  border: none;
  border-collapse: collapse;
  border-top: 1px solid #eee;
  clear: both;
  margin: 30px 0;
}

b,
strong {
  font-weight: 600;
}

blockquote,
cite,
em,
i {
  font-style: italic;
}

mark {
  background: #ddd;
  color: #000;
}

blockquote {
  margin: 30px;
}

/* Headings
--------------------------------------------- */

h1,
h2,
h3,
h4 {
  font-family: 'poynter-oldstyle-display', sans-serif;
  font-weight: 600;
  line-height: 1.2;
  margin: 0 0 20px;
}

h1 {
  font-size: 36px;
}

h2,
h3 {
  font-size: 30px;
}

h4 {
  font-size: 24px;
}

.entry-content h3,
.entry-content h4 {
  font-weight: 600;
  margin-top: 40px;
}

.entry-content h4 {
  margin-top: 40px;
}

/* Objects
--------------------------------------------- */

embed,
iframe,
img,
object,
video,
.wp-caption {
  max-width: 100%;
}

img {
  height: auto;
  vertical-align: top;
}

iframe {
  margin-bottom: 30px;
}

.calendar img {
  border: 1px solid #eee;
  padding: 9px;
}

.calendar img:focus,
.calendar img:hover {
  border: 1px solid #cbe3ef;
}

figure {
  margin: 0;
}

/* Forms
--------------------------------------------- */

input,
select,
textarea {
  background-color: #fff;
  border: 1px solid #000;
  color: #000;
  font-size: 18px;
  font-weight: 400;
  padding: 15px;
  width: 100%;
}

input:focus,
textarea:focus {
  background-color: #f5f5f5;
  border: 1px solid #000;
  outline: none;
}

input[type="checkbox"],
input[type="image"],
input[type="radio"] {
  width: auto;
}

.page-home input[type="email"],
.page-newsletter input[type="email"] {
  font-family: 'acumin-pro-extra-condensed', sans-serif;
  font-weight: 500;
  letter-spacing: 0.25px;
  margin-bottom: 10px;
  padding-bottom: 15px;
  padding-top: 13px;
}

.entry-content .nf-field-label label {
  font-family: 'acumin-pro-extra-condensed', sans-serif;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
}

::-ms-input-placeholder {
  color: #000;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #000;
  opacity: 1;
}

::placeholder {
  color: #000;
  opacity: 1;
}

a.button,
button,
input[type="button"],
input[type="reset"],
input[type="submit"],
.entry-content a.button,
.entry-content a.more-link {
  border: 1px solid #000;
  box-shadow: none;
  color: #000;
  cursor: pointer;
  font-family: 'acumin-pro-extra-condensed', sans-serif;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  padding: 15px 30px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  white-space: normal;
  width: auto;
}

a.more-link {
  display: inline-block;
}

.page-home input[type="submit"],
.page-newsletter input[type="submit"] {
  background-color: #000;
  color: #fff;
}

.page-home input[type="submit"]:hover,
.page-newsletter input[type="submit"]:hover,
.page-search .wp-block-search .wp-block-search__button:hover {
  background-color: #fff;
  color: #000;
}

a.button:focus,
a.button:hover,
button:focus,
button:hover,
input[type="button"]:focus,
input[type="button"]:hover,
input[type="reset"]:focus,
input[type="reset"]:hover,
input[type="submit"]:focus,
input[type="submit"]:hover,
.entry-content a.button:focus,
.entry-content a.button:hover,
.entry-content a.more-link:focus,
.entry-content a.more-link:hover {
  background-color: #000;
  border: 1px solid #000;
  box-shadow: none;
  color: #fff;
}

.entry-content .button:focus,
.entry-content .button:hover {
  color: #fff;
}

.button {
  display: inline-block;
}

.site-container button:disabled,
.site-container button:disabled:hover,
.site-container input:disabled,
.site-container input:disabled:hover,
.site-container input[type="button"]:disabled,
.site-container input[type="button"]:disabled:hover,
.site-container input[type="reset"]:disabled,
.site-container input[type="reset"]:disabled:hover,
.site-container input[type="submit"]:disabled,
.site-container input[type="submit"]:disabled:hover {
  background-color: #eee;
  border-width: 0;
  color: #777;
  cursor: not-allowed;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button {
  display: none;
}

/* Screen Reader Text
--------------------------------------------- */

.screen-reader-shortcut,
.screen-reader-text,
.screen-reader-text span {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus,
.widget_search input[type="submit"]:focus {
  background: #fff;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #000;
  display: block;
  font-size: 1em;
  font-weight: 700;
  height: auto;
  padding: 15px 23px 14px;
  text-decoration: none;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}


/* Structure and Layout
---------------------------------------------------------------------------- */

/* Site Container
--------------------------------------------- */

.site-container {
  animation: fadein 1s;
  word-wrap: break-word;
}

@keyframes fadein {

  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }

}

/* Site Inner
--------------------------------------------- */

.site-inner {
  clear: both;
  margin: 0 auto;
  padding-top: 60px;
}

.page-newsletter .site-inner {
  padding-bottom: 60px;
}


/* Common Classes
---------------------------------------------------------------------------- */

/* Avatar
--------------------------------------------- */

.avatar {
  border-radius: 50%;
  float: left;
}

.author-box .avatar,
.alignleft .avatar {
  margin-right: 20px;
}

.alignright .avatar {
  margin-left: 20px;
}

.comment .avatar {
  margin: 0 15px 20px 0;
}

/* Genesis
--------------------------------------------- */

.after-entry,
.archive-description,
.author-box {
  margin-bottom: 40px;
}

.after-entry {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
  padding: 20px 30px;
}

.after-entry .widget:last-of-type {
  margin-bottom: 0;
}

.author-box a {
  box-shadow: inset 0 -3px 0 0 #cbe3ef;
  color: #000;
  font-weight: 600;
  text-decoration: none;
}

.author-box a:focus,
.author-box a:hover {
  box-shadow: inset 0 -20px 0 0 #cbe3ef;
}

.breadcrumb {
  border-bottom: 1px solid #eee;
  font-size: 16px;
  margin-bottom: 40px;
  padding-bottom: 10px;
}

.archive-description p:last-child,
.author-box p:last-child {
  margin-bottom: 0;
}

/* Search Form
--------------------------------------------- */

.search-form {
  overflow: hidden;
}

.entry-content .search-form,
.page-search .wp-block-search {
  margin-left: auto;
  margin-right: auto;
  max-width: 400px;
}

.page-search .wp-block-search label {
  display: none;
}

.page-search .wp-block-search input[type="search"] {
  font-family: 'acumin-pro-extra-condensed', sans-serif;
  font-weight: 500;
  letter-spacing: 0.25px;
  text-align: center;
}

.page-search .wp-block-search input {
  border: none;
  border-bottom: 1px solid #000;
  margin-bottom: 20px;
}

.page-search .wp-block-search .wp-block-search__button {
  background-color: #000;
  color: #fff;
  display: inline-block;
  float: none;
  margin-left: auto;
  margin-right: auto;
}

.page-search .wp-block-search .wp-block-search__button:hover {
  background-color: #fff;
  color: #000;
}

.post-password-form input[type="submit"],
.search-form input[type="submit"] {
  margin-top: 10px;
}

.widget_search input[type="submit"] {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  width: 1px;
}

/* Titles
--------------------------------------------- */

.archive-description {
  margin-left: auto;
  margin-right: auto;
  max-width: 720px;
  text-align: center;
}

.archive-description .entry-title,
.archive-title,
.author-box-title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 10px;
}

.entry-title {
  font-size: 30px;
  margin-bottom: 10px;
}

.category-free-yoga-videos .entry-title {
  font-size: 24px;
}

.category .category-free-yoga-videos .entry-title,
.page-calendar .entry-title,
.page-search .entry-title {
  text-align: center;
}

.archive-description a.boxshadow {
  box-shadow: inset 0 -3px 0 0 #cbe3ef;
  color: #000;
  font-weight: 600;
  text-decoration: none;
}

.archive-description a.boxshadow:focus,
.archive-description a.boxshadow:hover {
  box-shadow: inset 0 -20px 0 0 #cbe3ef;
}

.entry-title a {
  color: #000;
  text-decoration: none;
}

.entry-title a:focus,
.entry-title a:hover {
  color: #7692a8;
}

.widget-title {
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 20px;
}

/* WordPress
--------------------------------------------- */

a.aligncenter img {
  display: block;
  margin: 0 auto;
}

a.alignnone {
  display: inline-block;
}

.alignleft {
  float: left;
  text-align: left;
}

.alignright {
  float: right;
  text-align: right;
}

a.alignleft,
a.alignnone,
a.alignright {
  max-width: 100%;
}

img.centered,
.aligncenter {
  display: block;
  margin: 0 auto 30px;
}

img.alignnone,
.alignnone {
  margin-bottom: 15px;
}

a.alignleft,
img.alignleft,
.wp-caption.alignleft {
  margin: 0 20px 20px 0;
}

a.alignright,
img.alignright,
.wp-caption.alignright {
  margin: 0 0 20px 20px;
}

.gallery-caption,
.wp-caption-text {
  font-size: 14px;
  font-weight: 600;
  margin: 0;
  text-align: center;
}

.entry-content p.wp-caption-text {
  margin-bottom: 0;
}

.entry-content .wp-audio-shortcode,
.entry-content .wp-playlist,
.entry-content .wp-video {
  margin: 0 0 30px;
}


/* Skip Links
---------------------------------------------------------------------------- */

.genesis-skip-link {
  margin: 0;
}

.genesis-skip-link .skip-link-hidden {
  display: none;
  visibility: hidden;
}

.genesis-skip-link li {
  height: 0;
  list-style: none;
  width: 0;
}

/* Display outline on focus */
:focus {
  color: #000;
  outline: #ccc solid 1px;
}


/* Site Container
---------------------------------------------------------------------------- */

.site-container {
  margin-left: auto;
  margin-right: auto;
  max-width: 1600px;
}


/* Site Header
---------------------------------------------------------------------------- */

.site-header {
  background-color: #fff;
  padding-bottom: 20px;
  padding-top: 20px;
}

.site-header>.wrap {
  padding-left: 30px;
  padding-right: 30px;
}

.site-header .header-social {
  display: none;
}

.site-header .header-social a {
  color: #000;
}

.site-header .header-social a:focus,
.site-header .header-social a:hover {
  color: #7692a8;
}

/* Title Area
--------------------------------------------- */

.title-area {
  float: left;
}

.site-title>a {
  background-size: 133px 40px;
  display: block;
  height: 40px;
  width: 133px;
}

.site-description,
.site-title {
  display: block;
  height: 0;
  margin-bottom: 0;
  text-indent: -9999px;
}


/* Site Navigation
---------------------------------------------------------------------------- */

.genesis-nav-menu {
  clear: both;
  line-height: 1;
  width: 100%;
}

.genesis-nav-menu .menu-item {
  display: block;
  float: none;
  position: relative;
}

.genesis-nav-menu a {
  color: #000;
  display: block;
  font-family: 'acumin-pro-extra-condensed', sans-serif;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  padding-bottom: 12px;
  padding-top: 12px;
  text-decoration: none;
  text-transform: uppercase;
}

.genesis-nav-menu .external a,
.genesis-nav-menu .sub-menu a {
  letter-spacing: 0;
  text-transform: none;
}

.genesis-nav-menu a:focus,
.genesis-nav-menu a:hover,
.genesis-nav-menu .current-menu-item>a,
.genesis-nav-menu .sub-menu .current-menu-item>a:focus,
.genesis-nav-menu .sub-menu .current-menu-item>a:hover {
  color: #7692a8;
  text-decoration: none;
}

.genesis-nav-menu .sub-menu,
.genesis-nav-menu .sub-menu a {
  width: 100%;
}

.genesis-nav-menu .sub-menu {
  clear: both;
  display: none;
  left: -9999px;
  margin: 0;
  opacity: 1;
  padding-left: 15px;
  position: static;
  z-index: 99;
}

.genesis-nav-menu .sub-menu a {
  background-color: #fff;
  padding-bottom: 10px;
  padding-top: 10px;
  position: relative;
  word-wrap: break-word;
}

.genesis-nav-menu .menu-item:focus,
.genesis-nav-menu .menu-item:hover {
  position: relative;
}

.genesis-nav-menu .menu-item:hover>.sub-menu {
  display: block;
  left: auto;
  opacity: 1;
}

/* Responsive Menu
--------------------------------------------- */

.menu .menu-item:focus {
  position: static;
}

.menu .menu-item>a:focus+ul.sub-menu,
.menu .menu-item.sfHover>ul.sub-menu {
  left: auto;
  opacity: 1;
}

.genesis-responsive-menu {
  display: none;
  position: relative;
}

.genesis-responsive-menu .genesis-nav-menu .menu-item:hover>.sub-menu {
  display: none;
}

.menu-toggle,
.sub-menu-toggle {
  background-color: transparent;
  border-width: 0;
  color: #000;
  display: block;
  font-family: 'acumin-pro-extra-condensed', sans-serif;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  margin: 0 auto;
  overflow: hidden;
  text-align: center;
  text-transform: uppercase;
  visibility: visible;
}

.menu-toggle:focus,
.menu-toggle:hover,
.sub-menu-toggle:focus,
.sub-menu-toggle:hover {
  background-color: transparent;
  border-width: 0;
  color: #7692a8;
}

.menu-toggle {
  float: right;
  line-height: 20px;
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 0;
  position: relative;
  z-index: 1000;
}

.menu-toggle.activated::before {
  content: "\f335";
}

.site-header .menu-toggle::before {
  float: left;
  margin-right: 5px;
  position: relative;
  text-rendering: auto;
  top: 1px;
}

.sub-menu-toggle {
  float: right;
  padding: 9px 10px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 100;
}

.sub-menu .sub-menu-toggle {
  padding: 12px 10px;
}

.sub-menu-toggle::before {
  display: inline-block;
  text-rendering: auto;
  transform: rotate(0);
  transition: transform 0.25s ease-in-out;
}

.sub-menu-toggle.activated::before {
  transform: rotate(180deg);
}

/* Header Menu
--------------------------------------------- */

.nav-primary {
  clear: left;
  width: 100%;
}

/* Footer Menu
--------------------------------------------- */

.nav-secondary {
  margin-top: 10px;
}

.nav-secondary .genesis-nav-menu {
  line-height: 1.5;
}

.nav-secondary .menu-item {
  display: inline-block;
}

.nav-secondary a {
  margin-left: 10px;
  margin-right: 10px;
  padding: 0;
}


/* Content Area
---------------------------------------------------------------------------- */

.content,
.page-home .wrap {
  max-width: 800px;
  padding-left: 30px;
  padding-right: 30px;
}

.page-home .home-welcome {
  background-color: #edebec;
  padding-bottom: 40px;
  padding-top: 40px;
}

.page-home .home-welcome .one-half.welcome-right {
  padding-bottom: 40px;
}

.page-home .home-welcome .one-half.welcome-right img {
  max-width: 75%;
}

.category-free-yoga-videos .content,
.page-about .content,
.page-home .content,
.page-home .wrap,
.page-wide .content {
  max-width: 1280px;
}

.category .content {
  max-width: 1040px;
}

.page-about .contain {
  margin-left: auto;
  margin-right: auto;
  max-width: 800px;
}

.page-home .contain,
.page-home .wp-block-embed-youtube {
  margin-left: auto;
  margin-right: auto;
  max-width: 720px;
}

.page-home .contain.eight {
  max-width: 800px;
}

/* Entry Content
--------------------------------------------- */

.entry {
  margin-bottom: 40px;
}

.category-blog .entry {
  border-bottom: 1px solid #eee;
  margin-bottom: 60px;
  padding-bottom: 60px;
}

.category-free-yoga-videos .entry {
  text-align: left;
}

.entry-content a {
  box-shadow: inset 0 -3px 0 0 #cbe3ef;
  color: #000;
  font-weight: 600;
  text-decoration: none;
}

.entry-content a.social,
.entry-content a.social:focus,
.entry-content .social:hover,
.entry-content .calendar a,
.entry-content .calendar a:focus,
.entry-content .calendar a:hover {
  box-shadow: none;
}

.entry-content a:focus,
.entry-content a:hover {
  box-shadow: inset 0 -20px 0 0 #cbe3ef;
}

.entry-content a.social {
  color: #000;
  font-size: 30px;
}

.entry-content a.social:focus,
.entry-content a.social:hover {
  color: #7692a8;
}

.entry-content ol,
.entry-content ul {
  margin-bottom: 30px;
  padding-left: 40px;
}

.entry-content ol>li {
  list-style-type: decimal;
}

.entry-content ul>li {
  list-style-type: disc;
}

.entry-content ol ul>li,
.entry-content ul ul>li {
  list-style-type: circle;
}

.entry-content ol ol,
.entry-content ul ul {
  margin-bottom: 0;
}

.entry-content .center {
  margin-bottom: 0;
  text-align: center;
}

.entry-image-blog {
  margin-bottom: 30px;
}

/* Entry Meta
--------------------------------------------- */

.entry-image {
  text-align: center;
}

p.entry-meta {
  font-size: 16px;
  margin-bottom: 0;
}

.entry-meta a {
  box-shadow: inset 0 -3px 0 0 #cbe3ef;
  color: #000;
  font-weight: 600;
  text-decoration: none;
}

.entry-meta a:focus,
.entry-meta a:hover {
  box-shadow: inset 0 -20px 0 0 #cbe3ef;
}

.entry-header .entry-meta {
  margin-bottom: 20px;
}

.entry-footer .entry-meta {
  border-top: 1px solid #eee;
  padding-top: 20px;
}

.entry-categories,
.entry-tags {
  display: block;
}

.entry-comments-link::before {
  content: "\2014";
  margin: 0 6px 0 2px;
}

/* Pagination
--------------------------------------------- */

.pagination {
  clear: both;
  margin: 60px 0;
  text-align: center;
}

.adjacent-entry-pagination {
  margin-bottom: 0;
}

.archive-pagination li {
  display: inline;
}

.archive-pagination li a {
  background-color: #fff;
  border: 1px solid #000;
  color: #000;
  cursor: pointer;
  display: inline-block;
  font-family: 'acumin-pro-extra-condensed', sans-serif;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  margin-bottom: 4px;
  padding: 8px 12px;
  text-decoration: none;
  text-transform: uppercase;
}

.archive-pagination li a:focus,
.archive-pagination li a:hover,
.archive-pagination li.active a {
  background-color: #000;
  color: #fff;
}

/* Entry Comments
--------------------------------------------- */

.comment-respond,
.entry-comments,
.entry-pings {
  margin-bottom: 40px;
}

.comment-list li {
  padding: 40px 0 0 30px;
}

.comment-list .depth-1 {
  padding-left: 0;
}

.comment-header {
  margin-bottom: 30px;
}

.comment-content {
  clear: both;
}

.comment-respond a,
.entry-comments a {
  box-shadow: inset 0 -3px 0 0 #cbe3ef;
  color: #000;
  font-weight: 600;
  text-decoration: none;
}

.comment-respond a:focus,
.comment-respond a:hover,
.entry-comments a:focus,
.entry-comments a:hover {
  box-shadow: inset 0 -20px 0 0 #cbe3ef;
}


.comment-content ul>li {
  list-style-type: disc;
}

.comment-respond input[type="email"],
.comment-respond input[type="text"],
.comment-respond input[type="url"] {
  width: 50%;
}

.comment-respond label {
  display: block;
  margin-right: 12px;
}

.comment-header p {
  margin-bottom: 0;
}

.entry-pings .reply {
  display: none;
}

.comment-form-cookies-consent label {
  display: inline;
  padding-left: 10px;
}


/* Site Footer
---------------------------------------------------------------------------- */

.site-footer {
  background-color: #fff;
  border-top: 1px solid #eee;
  font-size: 15px;
  line-height: 1.5;
  padding-bottom: 30px;
  padding-top: 30px;
  text-align: center;
}

.site-footer>.wrap {
  padding-left: 40px;
  padding-right: 40px;
}

.site-footer a,
.site-footer a:focus,
.site-footer a:hover {
  color: #000;
  text-decoration: none;
}

.site-footer a:focus,
.site-footer a:hover {
  color: #7692a8;
}

.site-footer p {
  font-family: 'acumin-pro-extra-condensed', sans-serif;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  margin-bottom: 0;
  text-transform: uppercase;
}

.site-footer .footer-copyright {
  margin-bottom: 20px;
}

.site-footer .footer-logo img {
  height: 30px;
  margin-bottom: 20px;
  width: 99px;
}

.site-footer .footer-social a {
  margin-left: 5px;
  margin-right: 5px;
}


/* Media Queries
---------------------------------------------------------------------------- */

@media only screen and (min-width: 960px) {

  /* Site Header
	--------------------------------------------- */

  .site-header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100%;
    z-index: 9999;
  }

  .shrink .site-header {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
    padding-bottom: 15px;
    padding-top: 15px;
  }

  .site-header>.wrap {
    margin-left: auto;
    margin-right: auto;
    padding-left: 40px;
    padding-right: 40px;
    max-width: 1280px;
  }

  /* Title Area
	--------------------------------------------- */

  .title-area {
    height: 40px;
    width: 20%;
  }

  /* Genesis Menu
	--------------------------------------------- */

  .genesis-nav-menu .menu-item {
    display: inline-block;
  }

  .genesis-nav-menu .menu-item:focus,
  .genesis-nav-menu .menu-item:hover {
    position: relative;
  }

  .genesis-nav-menu .sub-menu,
  .genesis-nav-menu .sub-menu a {
    width: 180px;
  }

  .genesis-nav-menu .sub-menu {
    border-top: 1px solid #eee;
    opacity: 0;
    padding-left: 0;
    position: absolute;
    transition: opacity 0.4s ease-in-out;
  }

  .genesis-nav-menu .sub-menu a {
    border: 1px solid #eee;
    border-top: 0;
    padding: 10px 15px;
    text-align: left;
  }

  .genesis-nav-menu .sub-menu .sub-menu {
    margin: -46px 0 0 179px;
  }

  /* Responsive Menu
	--------------------------------------------- */

  .genesis-responsive-menu {
    display: block;
  }

  .menu-toggle,
  .sub-menu-toggle {
    display: none;
    visibility: hidden;
  }

  /* Header Menu
	--------------------------------------------- */

  .nav-primary {
    clear: none;
    float: left;
    text-align: center;
    width: 60%;
  }

  .nav-primary .genesis-nav-menu a {
    padding-left: 10px;
    padding-right: 10px;
  }

  .nav-primary .genesis-nav-menu a,
  .nav-primary .genesis-nav-menu a:focus,
  .nav-primary .genesis-nav-menu a:hover {
    text-decoration: none;
  }

  .nav-primary .genesis-nav-menu .sep a {
    border-left: 1px solid #eee;
    margin-left: 10px;
    padding-left: 20px;
  }

  /* Header Social
	--------------------------------------------- */

  .site-header .header-social {
    display: block;
    float: right;
    margin-top: 5px;
    text-align: right;
  }

  .site-header .header-social a {
    margin-left: 10px;
  }

  /* Home Page
	--------------------------------------------- */

  .page-home .home-welcome {
    padding-bottom: 40px;
    padding-top: 60px;
  }

  .page-home .home-welcome .one-half.welcome-left {
    float: left;
    padding-top: 100px;
    width: 45%;
  }

  .page-home .home-welcome .one-half.welcome-right {
    float: right;
    width: 55%;
  }

  .page-home .home-welcome .one-half.welcome-right img {
    max-width: 660px;
  }

  .page-home .padding-left {
    padding-left: 40px;
  }

  .page-home .padding-right {
    padding-right: 40px;
  }

  /* Content
	--------------------------------------------- */

  .content,
  .page-home .wrap {
    margin-left: auto;
    margin-right: auto;
    padding-left: 40px;
    padding-right: 40px;
  }

  .category-free-yoga-videos .content {
    padding-left: 0;
    padding-right: 0;
  }

  h2,
  .archive-title,
  .entry-title {
    font-size: 48px;
  }

  .single .entry-title {
    font-size: 36px;
  }

  .page-home .widget-title {
    font-size: 60px;
  }

  h3 {
    font-size: 36px;
  }

  .category-blog .entry-title {
    font-size: 36px;
  }

  .category-free-yoga-videos .entry {
    float: left;
    padding-left: 40px;
    padding-right: 40px;
    width: 50%;
  }

  .category-free-yoga-videos .entry:nth-of-type(2n+1) {
    clear: left;
  }

  .entry-image-blog {
    float: left;
    margin-bottom: 0;
    margin-right: 40px;
    width: 300px;
  }

  .boxshadow.ab-block-container {
    background-color: #fff;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
    margin: -150px auto 0;
    max-width: 800px;
    padding: 5% 7.5%;
    position: relative;
    text-align: center;
  }

  /* About
	--------------------------------------------- */

  .page-about .connect {
    margin-left: auto;
    margin-right: auto;
    max-width: 680px;
  }

  .page-about .padding-right {
    padding-right: 40px;
  }

  /* Entry Header
	--------------------------------------------- */

  .single .entry-header {
    text-align: center;
  }

  .single .entry-header::after {
    border-bottom: 1px solid #000;
    content: "";
    display: block;
    margin-bottom: 40px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 10px;
    width: 2.5%;
  }

  /* Newsletter
	--------------------------------------------- */

  .page-home input[type="email"],
  .page-newsletter input[type="email"] {
    width: 300px;
  }

  .page-home .ck_email_field_group,
  .page-newsletter .ck_email_field_group {
    display: inline-block;
  }

  /* Author Box
	--------------------------------------------- */

  .author-box {
    background-color: #f5f5f5;
    padding: 30px;
  }

  /* After Entry
	--------------------------------------------- */

  .after-entry {
    padding: 40px 60px;
  }

  /* Entry Misc.
	--------------------------------------------- */

  .after-entry,
  .author-box,
  .comment-respond,
  .entry,
  .entry-comments,
  .entry-pings {
    margin-bottom: 60px;
  }

  .archive-description {
    margin-bottom: 100px;
  }

  .page-home input[type="email"],
  .page-newsletter input[type="email"],
  .page-newsletter .entry {
    margin-bottom: 0;
  }


  /* Site Footer
	--------------------------------------------- */

  .site-footer {
    text-align: left;
  }

  .site-footer>.wrap {
    margin-left: auto;
    margin-right: auto;
    max-width: 1280px;
  }

  .site-footer .footer-logo {
    float: left;
    width: 20%;
  }

  .site-footer .footer-copyright,
  .site-footer .footer-logo img {
    margin-bottom: 0;
  }

  .site-footer .footer-copyright {
    float: left;
    margin-top: 5px;
    text-align: center;
    width: 60%;
  }

  .site-footer .footer-social {
    float: right;
    margin-top: 5px;
    text-align: right;
    width: 20%;
  }

  .site-footer .footer-social a {
    margin-left: 10px;
    margin-right: 0;
  }

}